import Vue from 'vue'
import Vuex from 'vuex'
import Vue_Resource from 'vue-resource'

//  vue  vuex
Vue.use(Vuex)
Vue.use(Vue_Resource)

//  object 
const state = {
    username: '',
    types: [],
    rules: [],
    ruleId: null,
    appTypes: [],
    packages: [],
    idcs: []
}   

//  object  mutation 
const mutations = {

    // 
    GETUSER (state, data) {
        state.username = data
    },

    // 
    GETRUNTYPES (state, data) {
        state.types = data
    },

    // 
    GETRULES (state, data) {
        state.rules = data
    },

    // id
    GETRULEID (state, data) {
        state.ruleId = data
    },

    // 
    GETAPPTYPES (state, data) {
        state.appTypes = data
    },

    // 
    GETPACKAGES (state, data) {
        state.packages = data
    },

    // 
    GETIDCS (state, data) {
        state.idcs = data
    }
}

//  new Vuex.Store  state  mutations store
//  store  vue 
export default new Vuex.Store({
    state,
    mutations
})


